import Vue from "vue"
import App from "./App.vue"
import axios from "axios"
import {TableColumn,Table} from 'element-ui'
Vue.use(TableColumn);
Vue.use(Table);
//import 'element-ui/lib/theme-default/index.css'




// 将axios挂载到prototype上，在组件中可以直接使用this.axios访问
Vue.prototype.axios = axios;

new Vue({
  el: '#app',
  render: h => h(App)
})




