<template>
  <div id="app">
    <div class="el-row">
      <h1>{{msg}}</h1>
    </div>
    <el-table :data="tableData" border>
      <el-table-column prop="char" label="字符" width="100"></el-table-column>
      <el-table-column prop="description" label="描述"></el-table-column>
    </el-table>
  </div>
</template>

<script>
export default {
  name: 'app',
  data () {
    return {
      msg: '正则表达式速查表',
      tableData:[]
    }
  },
  mounted(){
    this.axios.get("src/data/data.json").then(
      response => {
        this.tableData = response.data;
    })
  }
}
</script>

<style>
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
}

h1{
  font-weight: normal;
}

</style>



